/**
 * Site header
 */
.site-header {
  border-top: 5px solid $grey-color-dark;
  border-bottom: 1px solid $grey-color-light;
  min-height: $spacing-unit * 1.865;
  line-height: $base-line-height * $base-font-size * 2.25;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  @include relative-font-size(1.625);
  font-weight: 300;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;

  @include media-query($on-palm) {
    padding-right: 45px;
  }

  &,
  &:visited {
    color: $grey-color-dark;
  }
}

.site-nav {
  position: absolute;
  top: 9px;
  right: math.div($spacing-unit, 2);
  background-color: $background-color;
  border: 1px solid $grey-color-light;
  border-radius: 5px;
  text-align: right;

  .nav-trigger {
    display: none;
  }

  .menu-icon {
    float: right;
    width: 36px;
    height: 26px;
    line-height: 0;
    padding-top: 10px;
    text-align: center;

    > svg path {
      fill: $grey-color-dark;
    }
  }

  label[for='nav-trigger'] {
    display: block;
    float: right;
    width: 36px;
    height: 36px;
    z-index: 2;
    cursor: pointer;
  }

  input ~ .trigger {
    clear: both;
    display: none;
  }

  input:checked ~ .trigger {
    display: block;
    padding-bottom: 5px;
  }

  .page-link {
    color: $text-color;
    line-height: $base-line-height;
    display: block;
    padding: 5px 10px;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 0;
    }
    margin-left: 20px;
  }

  @media screen and (min-width: $on-medium) {
    position: static;
    float: right;
    border: none;
    background-color: inherit;

    label[for='nav-trigger'] {
      display: none;
    }

    .menu-icon {
      display: none;
    }

    input ~ .trigger {
      display: block;
    }

    .page-link {
      display: inline;
      padding: 0;

      &:not(:last-child) {
        margin-right: 20px;
      }
      margin-left: auto;
    }
  }
}

/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: math.div($spacing-unit, 2);
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: math.div(-$spacing-unit, 2);
  @extend %clearfix;
}

.footer-col {
  width: -webkit-calc(100% - (#{$spacing-unit} / 2));
  width: calc(100% - (#{$spacing-unit} / 2));
  margin-bottom: math.div($spacing-unit, 2);
  padding-left: math.div($spacing-unit, 2);
}

.footer-col-1,
.footer-col-2 {
  width: -webkit-calc(50% - (#{$spacing-unit} / 2));
  width: calc(50% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: -webkit-calc(100% - (#{$spacing-unit} / 2));
  width: calc(100% - (#{$spacing-unit} / 2));
}

@media screen and (min-width: $on-large) {
  .footer-col-1 {
    width: -webkit-calc(35% - (#{$spacing-unit} / 2));
    width: calc(35% - (#{$spacing-unit} / 2));
  }

  .footer-col-2 {
    width: -webkit-calc(20% - (#{$spacing-unit} / 2));
    width: calc(20% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(45% - (#{$spacing-unit} / 2));
    width: calc(45% - (#{$spacing-unit} / 2));
  }
}

@media screen and (min-width: $on-medium) {
  .footer-col {
    float: left;
  }
}

/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
  flex: 1 0 auto;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: math.div($spacing-unit, 1.5);

    > h3 {
      margin: 0;
    }

    > p {
      font-size: $small-font-size * 1.05;
    }
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  font-weight: bold;
  @include relative-font-size(1.2);
  margin-bottom: 2px;

  &:hover {
    text-decoration: none;
    box-shadow: none;
  }
}

/**
 * Posts
 */
.post-header {
  margin-bottom: math.div($spacing-unit, 2);
}

.post-title,
.post-content h1 {
  @include relative-font-size(2);
  letter-spacing: -1px;
  line-height: 1;
  font-weight: bold;

  @media screen and (min-width: $on-large) {
    @include relative-font-size(2.25);
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(1.5);
    // @include background-opacity($theme-color, 0.5);
    // background: darken($theme-color, 10%);
    background: lighten($theme-color, 30%);
    padding: math.div($spacing-unit, 5);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.25);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.05);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(1.125);
    }
  }
}

.social-media-list {
  display: table;
  margin: 0 auto;
  li {
    float: left;
    margin: 0 5px;
    &:first-of-type {
      margin-left: 0;
    }
    &:last-of-type {
      margin-right: 0;
    }
    a {
      display: block;
      padding: math.div($spacing-unit, 4);
      border: 1px solid $grey-color-light;
    }
    &:hover .svg-icon {
      fill: currentColor;
    }
  }
}

/**
 * Grid helpers
 */
@media screen and (min-width: $on-large) {
  .one-half {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width: calc(50% - (#{$spacing-unit} / 2));
  }
}

/**
 * Pagination
 */
.pagination {
  a {
    padding: math.div($spacing-unit, 10) math.div($spacing-unit, 3);
    text-decoration: none;

    &:hover {
      color: $background-color;
      background-color: $klavier-gray;
    }
  }
}

.pagination_active {
  color: $background-color;
  background-color: $klavier-gray;
  padding: math.div($spacing-unit, 10) math.div($spacing-unit, 3);

  &:hover {
    color: $background-color;
  }
}

/**
 * Archive
 */
.highligher-rouge {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  border-radius: 1px;
  background: $tag-back-color;
  padding: 0 4px;
  margin: 2px 0;
  display: inline-block;

  & > a {
    color: #fff;
    box-shadow: none;
  }
}

/**
 * 返回顶部
 */
.top {
  position: fixed;
  right: calc((100% - 960px) / 3);
  bottom: 50px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  opacity: 0.4;
  display: flex;
  justify-context: center;
  &:hover {
    opacity: 1;
  }
  &__tri {
    position: relative;
    width: 0;
    height: 0;
    border: 15px solid;
    border-color: transparent transparent $orange-color transparent;
  }
}

/**
 * 图片 Image
 */
.img {
  &--center {
    margin: 0 auto;
    display: block;
  }
}
